    <div v-else-if="lang===`en`">
      <h4 class="fw-bold"> The only such brigade</h4>

      <p class="fw-bold">
        After the defeat of France at the end of June 1940, the British expected that thousands of French soldiers and a handful of Poles would move from Syria to Palestine under their control. What was their amazement when they saw that it was just the opposite. Here, in full order and with weapons, the Carpathian Rifle Brigade came to their aid. The beginning of the existence of the Carpathian Rifle Brigade is given the order to form it, which was announced by the Commander-in-Chief, General Władysław Sikorski on April 12, 1940. The fact is, however, that the creation of Polish troops in the Middle East with the local French army was thought of by the staff of the Commander-in-Chief from the beginning of this year. In January, the military attaché in Romania and Hungary were ordered to develop a plan for the evacuation of Polish troops to the Levant (the plan was officially called Evacuation B), as the African coasts of the Mediterranean were called at that time. Just two days after the order on the organization of the Polish brigade at the Army of the Levant under General Weygand, on April 14, the organizational and instructor staff departed to Beirut - a total of 32 officers and 20 privates. At the end of that month, certified colonel Stanisław Kopański, appointed as the brigade commander, arrived in the capital of Lebanon. According to the full-time jobs, the Carpathian Rifle Brigade was to have nearly seven thousand soldiers and was to be a mountain unit. The adjective "Karpacka" in the name was supposed to describe the direction from which it would come to liberate the homeland, but at the same time it well reflected the experiences of many of its soldiers who had to break through the Carpathians in order to reach it.


      </p>
      <h4 class="fw-bold"> 

Intelligent brigade
</h4>
      <p>
        Soon, volunteers began to report to a military camp near the Syrian city of Homs. Most of them were people not only hardened by the dramatic experiences of recent months, but also educated. The brigade had the highest percentage of soldiers with higher education in the entire Polish army, and with military experience. As it was emphasized in the official history of the unit: "In the ranks of the brigade there were not only September soldiers, but also a number of those who had had the French and Norwegian campaigns behind them, and even exotic fights in East Africa and the Far East in the Foreign Legion units." Such soldiers wanted to fight the Germans as soon as possible, but their French ally completely disappointed them in this respect. When the news of France's surrender reached Syria on June 19, 1940, the command of the Army of the Levant sided with Marshal Pétain, who agreed to collaborate with Germany. Faced with the threat of disarmament and internment of the Polish brigade, Brigadier General Stanisław Kopański (promoted to this degree on May 3) decided to act decisively and at all costs get to British-controlled Palestine. Fortunately, the Chief of Staff of the Levant Army, Colonel de Larminat, proved loyal to his allies and, on his own responsibility, equipped the brigade with weapons, ammunition and transport equipment, including more than 100 trucks. So equipped, the Polish brigade caused a sensation among British soldiers who welcomed it to Palestine.
      </p>

      <h4 class="fw-bold">The rats of Tobruk</h4>
      <p>
        Polish soldiers were sent to the Latrun camp, where they were equipped with British equipment and weapons. Compared to the French shortcomings and mess, the British quartermaster was a nice contrast for the Poles. In October 1940, part of the brigade was transported to Egyptian Alexandria, where it was used to build fortifications and guard prisoner-of-war camps with captured Italians. The British had a problem with sending the brigade to the front at first, as Poland was not at war with Italy, but when the Afrika Korps tanks of General Erwin Rommel, the future "Desert Fox", appeared in the desert, these doubts lost their relevance. On January 12, 1941, the reorganization of the brigade according to British patterns was completed and from then on it was called the Independent Carpathian Rifle Brigade. In view of the German invasion of Greece in the spring of 1941, it was decided to transfer it to the Greek front. Mountain training and rearming of mountain troops began, but before it was completed, Greece fell. At the same time, on the African front, General Rommel launched his offensive, so the brigade was quickly transferred to the fortress of Marsa Matruh, and then to the fortified Sidi Baggush. Here, Poles encountered such exotic soldiers as South Africans, Australians, Indians, New Zealanders, soldiers of the Free French Foreign Legion - all nations of the British Empire and its allies in Africa. It was a great competition in gaining military fame, especially since South African and Australian soldiers, not to mention the Foreign Legion, had much more experience in desert fights. And yet, when the Carpathians joined the Tobruk crew at the end of August 1941, they proved that they were an exceptionally tough and fighting enemy for the Germans and Italians besieging this key fortress in Libya. At the beginning of October, the brigade had the heaviest section of defense - the so-called a breach in the area of the Ras el Medauar hill. The enemy had great shooting positions on the hill, and the distance between the fighters was on average 800, and here and there 300-200 meters! This is what General Kopański writes about it in his memoirs: "... the commander of the fortress asked me how long the Carpathian Brigade soldiers could stay in the breach, because in view of the likelihood of an 8th Army offensive in early November, it will be difficult for him to regroup [...]. I replied that the Australian record of staying in the breach was four weeks; that the Poles could withstand a week longer […]. At that time, I could not even have imagined that as a result of postponing the start of the offensive, and then its slow advance, Carpathian soldiers would set a record of ten-week stay in the breach! Neither of them will forget Medauar for the rest of their lives ”.</p>

      <h4 class="fw-bold">Elite of the 2nd Corps</h4>
      <p>
        
In Tobruk, the Carpathians, with their combat patrols and uhlan imagination (which is not only a metaphor, because the soldiers of the Carpathian Lancers' Regiment particularly distinguished themselves there) fully deserved the honorable title of Tobruk Rats, but after the fortress was unblocked in December 1941 by the abovementioned British attack 8 The army faced new tasks. A large part of the Polish brigade (without the Carpathian lancers) became part of the 13th Corps of the 8th Army and again distinguished itself, breaking the enemy's defense lines at Gazala. Thanks to this, the 8th Army was able to launch a wide offensive and displace the Germans from Cyrenaica. From New 1942, individual units of the Carpathian brigade supported the British in their hard fights with the relentless Rommel. Brigade artillery crushed the fortifications of the fortress in Bardia. Its walkers along with the Free French defended the El Mekili fort, and later found themselves again in Gazala and held bravely positions at Carmuset et Regem. Finally, it's time to rest and reorganize. In March 1942, the brigade was withdrawn from the line and directed first to the El Amiriya camp and then to Palestine. It was also connected with the fact that in the meantime the evacuation of General Anders's Army from the Soviet Union was already underway. In May 1942, the Independent Carpathian Rifle Brigade was disbanded, and its cadre became the backbone of the 3rd Carpathian Rifle Division of the Polish Army in the East, and then the 2nd Polish Corps. The soldiers who had fought in the desert behind them were called Ramses by the comrades-in-arms who came from the Soviets. Initially, there were some antagonisms between the Ramses and the Orthodox (or buzułukami, as the soldiers of the Polish Army in the USSR were called). The Ramses were accused of exalting themselves and glorifying themselves with the war glory they had won in Tobruk and at Gazala. But all of that was over when the 3rd Carpathian Rifle Division was in Italy at the end of 1943. On the Italian front, the Carpathians had more than enough opportunities to confirm their combat value and win more laurels in combat.


      </p>

